import React from "react";

interface INumAreaProps {
  areaTransform: string;
  titleTransform: string;
  tspan: string;
  title: number;
  fillColor: string;
  key: number;
  activeButtonList: Array<number>;
}

const NumArea: React.FC<INumAreaProps> = ({
  areaTransform,
  titleTransform,
  tspan,
  title,
  fillColor,
  activeButtonList,
}) => {
  return (
    <g>
      <g transform={areaTransform}>
        <g
          className="transition-colors duration-300"
          fill={`${activeButtonList.indexOf(title) !== -1 ? "#B18F12" : fillColor}`}
          stroke="#b9a978"
          strokeWidth="1"
          opacity="0.8"
        >
          <rect width="58" height="58" rx="3" stroke="none" />
          <rect x="0.5" y="0.5" width="57" height="57" rx="2.5" fill="none" />
        </g>
      </g>
      <text
        transform={titleTransform}
        fill="#e7e7e7"
        fontSize="30"
        fontWeight={600}
        fontFamily="Montserrat"
      >
        <tspan x={tspan} y="0">
          {title}
        </tspan>
      </text>
    </g>
  );
};

export default NumArea;
