import React from 'react'

interface IRowCellAreaProps {
  id: string,
  key: string,
  areaTransform: string,
  titleTransform: string,
}

const RowCellArea:React.FC<IRowCellAreaProps> = ({areaTransform, titleTransform}) => {
  return (
    <g>
      <g transform={areaTransform} fill="#2d2d2d" stroke="#8d966c" strokeWidth="1" opacity="0.78">
        <rect width="44" height="58" rx="3" stroke="none"/>
        <rect x="0.5" y="0.5" width="43" height="57" rx="2.5" fill="none"/>
      </g>
      <text transform={titleTransform} fill="#958963" fontSize="18" fontWeight={600} fontFamily="Montserrat"><tspan x="0" y="0">2:1</tspan></text>
    </g>
  )
}

export default RowCellArea